body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --bg-color: rgba(150, 150, 150, 1);
  --color-0: #282c34;
  --color-1: #ff6600;
  --color-2: #eec900;
  --color-3: #0000ff;
  --color-4: #cc00ff;
  --color-5: #00ff00;
  --color-6: #66ccff;
  --color-7: #ff0000;
  --color-border: #3b3c3d;
  --cols: 10;
  --rows: 18;
  --tile-size: 5vw;
  --border-width: 2px;
  --button-radius: 0%;
}
@media screen and (min-width: 768px) {
  :root {
    --tile-size: 20px; /* PC端的值 */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .App {
    width: 500px;
    padding-left: 4vw;
    height: 667px;
  }
}

.color-0 {
  background-color: var(--color-0);
}

.color-1 {
  background-color: var(--color-1);
}

.color-2 {
  background-color: var(--color-2);
}

.color-3 {
  background-color: var(--color-3);
}

.color-4 {
  background-color: var(--color-4);
}

.color-5 {
  background-color: var(--color-5);
}

.color-6 {
  background-color: var(--color-6);
}

.color-7 {
  background-color: var(--color-7);
}

* {
  box-sizing: border-box;
}

.App {
  --col-side: calc(var(--tile-size) * 5);
  --col-center: calc(var(--tile-size) * 12);
  --grid-height: calc(var(--tile-size) * 18);
  margin: auto;
  display: grid;
  grid-gap: var(--tile-size) var(--tile-size);
  grid-template-columns: var(--col-side) var(--col-center);
  grid-template-rows: 50px calc(var(--tile-size) * 4) var(--grid-height) 100px;
  grid-template-areas:
    'h h '
    'l r '
    'c c '
    'b b ';
}
